.desktop-layout {    
    display: grid;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    grid-template-columns: minmax(auto, 370px) auto;
    grid-template-rows: auto 1fr auto;
    grid-template-areas: 'sidenav main';
    height: 100vh;
  
    aside {
        grid-area: sidenav;
        background-color: #30475E;
        width: rem-calc(200px);
    }

    main {
        grid-area: main;
        // padding: 20px;
    }

}
